.App {
  h1 {
    text-align: center;
  }
  .movie {
    margin: 0 50px 10px 50px;
    display: flex;
    align-items: center;
    padding: 7px;
    justify-content: space-between;
    margin-bottom: 10px;
    border: 1px solid #ddd;

    .movie-title {
      flex-grow: 1;
    }
    .movie-year {
      color: rgb(172, 46, 134);
      font-size: 0.6rem;
    }
  }
  .lefttext {
    margin-left: 50px;
  }
  form {
    padding: 5px 40px;
    input {
      width: 100%;
      padding: 10px 8px;
      box-sizing: border-box;
      margin-bottom: 5px;
    }
    button {
      width: 100%;
      background: rgb(172, 46, 134);
      color: #fff;
      padding: 10px 0;
      margin-bottom: 10px;
      border: none;
    }
    .Err {
      color: red;
      font-size: 10px;
      margin-bottom: 15px;
      margin-left: 5px;
    }
  }
  nav {
    width: 100%;
    background: #41f06d;
    padding: 20px 0;
    ul {
      display: flex;
      list-style: none;
      li {
        padding: 0 20px;

        a {
          text-decoration: none;
          color: #fff;
        }
      }
    }
  }
}
